import React, { Component } from 'react';
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import { baseUrl } from '../../../../Assets/AssetsHelper';

class SelectedFeed extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        isActiveAside: false,
        feedData: []
    };

    onLoadFeedList = () => {
        var self = this;
        getFeedsData().then((response) => {
            const { responseData } = response.data;
            self.setState({ feedData: responseData })
        }).catch((ex) => {

        })
    }
    componentDidMount() {
        this.onLoadFeedList()
    }
    render() {
        const { feedData } = this.state
        return  <div className="row gx-3">
                    {feedData && Object.keys(feedData).length > 0 && feedData.feeds.length > 0 ? feedData.feeds.map((item) => {
                        return item.name ? <div key={item.feedId} className="col-sm-6 col-lg-4">
                            <div className="d-flex align-items-start justify-content-between mb-3">
                                <label className="form-check mb-xxl-2 mb-1 flex-grow-1 overflow-hidden w-100">
                                    <input className="form-check-input" type="checkbox" name={item.feedId} value={item.feedId} />
                                    <span className="form-check-label text-ellipsis">{item.name}</span>
                                </label>
                                <span className="badge text-muted border rounded-4 min-w-30px fs-9">{item.count}</span>
                            </div>
                        </div> : null
                    }) : null
                    }
                    
                </div>

    }
}

export default SelectedFeed;